.KFVideo-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.KFVideo-container-video{
    width: 50%
}

.KFVideo-container video {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    z-index: -4;
}

.KFVideo-container-text{
    height: 75%;
    width: 50%;
