}


.KFVideo-overlap-text h2{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: rgb(189, 154, 82);
    text-transform: uppercase;
    font-size: 28px;
    /* height: 100%; */
    margin: 0px;
    padding-right: 20px;
}


.KFVideo-body-text p{
    font-weight: 500;
    margin-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
}


